<template>
<div class="plain-element">
<div id="page-index">
  <div class="row plain-element">
      <TopMenuComponent
              :currentYear=getCurrentYear()
              :currentMonth = getCurrentMonth()
      />
  </div>
  <div v-if="getWeeksList()" class="row plain-element">
    <div v-if="getMonthList()"  class="plain-element">
      <SidebarComponent
              :currentYear=getCurrentYear()
              :currentMonth = getCurrentMonth()
      />
    </div>
    <div v-else class="col sidebar">
    </div>
    <div  v-if="getCurrentWeekData()" class="col s9 m12 l10 plain-element">
      <div class="dashboard-charts">
        <div class="col m12 l8 plain-element">
        <div v-if="getWeeksList()" class="row plain-element">
              <WeekMenuComponent
                  :weekList= getWeeksList()
                  :currentMonth = getCurrentMonth()
                  :currentYear = getCurrentYear()
                  :currentWeek = getCurrentWeek()
              />
        </div>
        <div v-else class="row plain-element">
            <div class="row week-menu">
            </div>
        </div>

        <div class="row plain-element">
          <div class="col s4 m4 plain-element">
            <div class="card insights-small left">
              <table>
                <td>Avg. Daily Completion:</td>
                  <td style="text-align: right"><b>
                    <i-count-up
                    :start="0"
                    :endVal="getCurrentWeekData().avg_submissons"
                    :duration="2"
                    :callback="callback"
                    ></i-count-up>
                  </b></td>
              </table>
            </div>
          </div>
          <div class="col s4 m4 plain-element">
            <div class="card insights-small">
              <table>
                <td>Ground Modules:</td>
                <td style="text-align: right"><b>
                    <i-count-up
                    :start="0"
                    :endVal="getCurrentWeekData().ground_count"
                    :duration="2"
                    :callback="callback"
                    ></i-count-up>
                  </b></td>
              </table>
            </div>
          </div>
          <div class="col s4 m4 plain-element">
            <div class="card insights-small right">
              <table>
                <td>Orbit Modules:</td>
                <td style="text-align: right"><b>
                    <i-count-up
                    :start="0"
                    :endVal="getCurrentWeekData().orbit_count"
                    :duration="2"
                    :callback="callback"
                    ></i-count-up>
                  </b></td>
              </table>
            </div>
          </div>
        </div>
        <div class="row plain-element">
          <div class="card insights-card">
           <div class="card-header">
              <div class="col s4 m6 plain-element">
               <p>List of Modules</p>
              </div>
              <div class="col s8 m6 plain-element right-align">
                <div class="row searchbox-wrapper searchbox-long">
                    <input class="form-control" id="searchbox" type="text" placeholder="Module Filter"
                           aria-label="Search for Module" v-model="search">
                </div>
              </div>
            </div>
            <table id="moduleTable" class="table-modules">
              <thead>
              <tr>
                <th onclick="sortTable(0)">Project</th>
                <th onclick="sortTable(1)" class="text-center">Manufacturer</th>
                <th onclick="sortTable(2)" class="text-center">Module Type</th>
                <th onclick="sortTableInt(3)" class="text-center">Budget</th>
                <th class="text-center">Rev.</th>
                <th class="text-center">Overhead</th>
                <th class="text-center">Budget Adj.</th>
              </tr>
              </thead>
              <tbody>
                <tr v-for="module in filteredModuleList" :key="module.pk">
                  <td class="text-left td-title">
                    <router-link :to="{ name: 'module-data', params: {moduleString: getModuleString(module.module_link)}}">
                      {{ module.phase_title }}
                    </router-link>
                  </td>
                  <td class="text-center">
                    <img v-if="module.module_brand != 'Not Specified'" :src="'https://productivity-dashboard.s3-eu-west-1.amazonaws.com/static/img/brands/' + module.module_brand + '.png'" class="img img-module">
                    <img v-else :src="'https://productivity-dashboard.s3-eu-west-1.amazonaws.com/static/img/brands/Septellar.png'" class="img img-module">
                  </td>
                  <td class="text-center">
                      <i v-if="module.module_file_type == 'Orbit'" class="fas fa-space-shuttle" style="color: #0073b1"></i>
                      <i v-else class="fas fa-globe-americas" style="color: #3c4146"></i>
                  </td>
                  <td class="text-center">{{ module.budget }}</td>
                  <td v-if="module.revision != 1" class="text-center">{{ module.revision }}</td>
                  <td v-if="module.revision != 1" class="text-center">{{ module.budget_difference }}</td>
                  <td v-else class="text-center new" colspan="3"> Completed on Budget</td>
                  <td v-if="module.revision != 1" class="text-center">{{ module.budget_adjustment }}</td>

                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="col s12 m12 l4 plain-element">
          <div class="row week-menu"> </div>
          <div class="row plain-element">
            <div class="card insights-card insights-card-narrow-right">
              <div class="card-header">
                <p>Weekly Summary</p></div>
              <div class="chart chart-long">
              <table class="table-summary">
                    <tr>
                      <td>Modules Total:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().module_count}}</b></td>
                    </tr>
                    <tr>
                      <td>New Modules:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().new_modules}}</b></td>
                    </tr>
                    <tr>
                      <td>Module Re-Engineering:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().module_edits}}</b></td>
                    </tr>
                    <tr>
                      <td>Total Budget Added:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().budget_added}}</b></td>
                    </tr>
                    <tr>
                      <td>Total Budget Adjusted:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().budget_adjusted}}</b></td>
                    </tr>
                    <tr>
                      <td>Max. Budget Difference:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().max_difference}}</b></td>
                    </tr>
                    <tr>
                      <td>Max. Budget Adjustment:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().max_adjustment}}</b></td>
                    </tr>
                    <tr>
                      <td>Lowest Budget:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().shortest_completion}}</b></td>
                    </tr>
                    <tr>
                      <td>Highest Budget:</td>
                      <td class="text-center"><b>{{getCurrentWeekData().longest_completion}}</b></td>
                    </tr>
                  </table>

              </div>
            </div>
          </div>
        </div>
     </div>
     </div>
     <div v-else  class="plain-element">
        <div class="plain-element">
          <div class="col sidebar"></div>
        </div>

        <div class="col s9 m10 plain-element">
          <div class="dashboard-charts">
          </div>
      </div>
    </div>
  </div>
</div>

</div>

</template>

<script>
import ICountUp from 'vue-countup-v2';

import SidebarComponent from "@/components/SidebarComponent.vue"
import TopMenuComponent from "@/components/TopMenuComponent.vue"

import WeekMenuComponent from "@/components/WeekMenuComponent.vue"

import { mapGetters, mapActions } from "vuex";

export default {
  name: 'Week',
  components: {
    SidebarComponent,
    TopMenuComponent,
    WeekMenuComponent,
    ICountUp,
  },
  props: {
    year: {
      type: String,
      required: true,
    },
    month: {
      type: String,
      required: true,
    },
    week: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      search: ""
    }
  },
  methods: {
     ...mapGetters([ "getMonthList","getCurrentYear","getCurrentMonth", "getCurrentWeekData", "getWeeksList",
                    "getModuleList", "getCurrentWeek"]),
    ...mapActions(["fetchWeeksData", "fetchModulesData" ]),
    callback: function (ins) {
        ins.update(ins.endVal + 100)
    },
    getModuleString(moduleLink) {
      var linkList = moduleLink.split("/")
      return linkList[linkList.length -1]
    },
  },
  computed: {
     filteredModuleList() {
      return this.getModuleList().filter(module => {
        return module.phase_title.toLowerCase().includes(this.search.toLowerCase()) ||
               module.module_brand.toLowerCase().includes(this.search.toLowerCase()) ||
               module.module_file_type.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  created() {
      this.fetchWeeksData({"year": this.year, "month": this.month, "week": this.week});
      this.fetchModulesData({"year": this.year, "month": this.month, "week": this.week});
      document.title = "Week " + this.week + "/" + this.year;
  },
  beforeRouteUpdate (to, from, next) {
    this.chosenMonth = to.params.month
    this.chosenWeek = to.params.week
    this.chosenYear = to.params.year
    this.fetchWeeksData({"year": this.chosenYear, "month": this.chosenMonth, "week": this.chosenWeek});
    this.fetchModulesData({"year": this.chosenYear, "month": this.chosenMonth, "week": this.chosenWeek});
    document.title = "Week " + this.chosenWeek + "/" + this.chosenYear;
    next();
  }
}
</script>