<template>
<div v-if="weekList.length < 6" class="row week-menu">
      <div class="col s2 plain-element">
        <router-link :to="{ name: 'month-data', params: {year: currentYear, month: currentMonth }}">
        <p> {{ monthMapperDict[currentMonth] }}</p>
        </router-link>
      </div>
      <div v-for="(element, index) in weekList" :key="index" class="col s2 plain-element">
          <router-link :to="{ name: 'week-data', params: {week: element.toString(), year: currentYear, month: currentMonth }}">
            <p v-if="element == currentWeek"><b>Week {{ element }}</b></p>
            <p v-else>Week {{ element }}</p>
          </router-link>
      </div>
</div>
<div v-else class="row week-menu">
      <div v-for="(element, index) in weekList" :key="index" class="col s2 plain-element">
          <router-link :to="{ name: 'week-data', params: {week: element, year: currentYear, month: currentMonth}}">
            <p v-if="element == currentWeek"><b>Week {{ element }}</b></p>
            <p v-else>Week {{ element }}</p>
          </router-link>
      </div>
</div>
</template>



<script>

import { monthMapperDict } from "@/common/MonthMapper.js"


export default {
  name: "WeekMenuComponent",
  props: {
    weekList: {
      type: Array,
      required: true,
    },
    currentWeek: {
      type: String,
      required:false,
    },
    currentMonth: {
      type: String,
      required:false,
    },
    currentYear: {
      type: String,
      required:true,
    }

  },
  data() {
    return {
      monthMapperDict : monthMapperDict
    }
  },
  methods: {

  },
}

</script>