import { apiService } from "@/common/api.service.js";

export default {
  userInfo() {
    let endpoint = "/auth/current-user/";
    return apiService(endpoint);
  },

  currentYearData() {
    let endpoint = "/api/current-year/";
    return apiService(endpoint);
  },
  monthsData(year) {
    let endpoint = `/api/get-months/${year}/`;
    return apiService(endpoint);
  },
  annualData(year) {
    let endpoint = `/api/year/${year}/`;
    return apiService(endpoint);
  },
  monthData(year, month) {
    let endpoint  = `/api/month/${year}/${month}/`;
    return apiService(endpoint);
  },
  weeksData(year, month) {
    let endpoint = `/api/get-weeks/${year}/${month}/`;
    return apiService(endpoint);
  },
  modulesData(year, month, week) {
    let endpoint = `/api/week/${year}/${month}/${week}/`;
    return apiService(endpoint);
  },
  moduleData(moduleString) {
    let endpoint = `/api/module/${moduleString}/`;
    return apiService(endpoint);
  }

}