import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import NotFound from "./views/NotFound.vue";
import Year from "./views/Year.vue";
import Month from "./views/Month.vue";
import Week from "./views/Week.vue";
import ModuleData from "./views/ModuleData.vue";

Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/year/:year",
      name: "year-data",
      component: Year,
      props: true
    },
    {
      path: "/month/:year/:month",
      name: "month-data",
      component: Month,
      props: true
    },
    {
      path: "/week/:year/:month/:week",
      name: "week-data",
      component: Week,
      props: true
    },
    {
      path: "/module-data/:moduleString",
      name: "module-data",
      component: ModuleData,
      props: true
    },
    {
      path: "*",
      name: "page-not-found",
      component: NotFound,
    },
  ]
})
