<template>
<div class="plain-element">
  <div id="page-index">
    <div class="row plain-element">
        <TopMenuComponent
            :currentYear=getCurrentYear()
            :currentMonth = getCurrentMonth()
        />
    </div>
    <div v-if="getCurrentMonth()"  class="row plain-element">
      <div  class="plain-element">
        <SidebarComponent
                :currentYear=getCurrentYear()
                :currentMonth = getCurrentMonth()
        />
      </div>
      <div class="col s9 m12 l10 plain-element">
        <div class="dashboard-charts">
          <div v-if="getWeeksList()" class="col m12 l8 plain-element">
            <div class="row plain-element">
              <WeekMenuComponent
                  :weekList= getWeeksList()
                  :currentMonth = getCurrentMonth()
                  :currentYear = getCurrentYear()
              />
            </div>
            <div class="row plain-element">
              <div class="col s4 m4 plain-element">
                <div class="card insights-small left">
                  <table>
                    <td>Avg. Daily Completion:</td>
                    <td style="text-align: right"><b>
                      <i-count-up
                      :start="0"
                      :endVal="getCurrentMonthData().avg_submissons"
                      :duration="2"
                      :callback="callback"
                      ></i-count-up>
                    </b></td>
                  </table>
                </div>
              </div>
              <div class="col s4 m4 plain-element">
                <div class="card insights-small">
                  <table>
                    <td>Ground Modules:</td>
                     <td style="text-align: right"><b>
                      <i-count-up
                      :start="0"
                      :endVal="getCurrentMonthData().ground_count"
                      :duration="2"
                      :callback="callback"
                      ></i-count-up>
                    </b></td>
                  </table>
                </div>
              </div>
              <div class="col s4 m4 plain-element">
                <div class="card insights-small right">
                   <table>
                    <td>Orbit Modules:</td>
                    <td style="text-align: right"><b>
                      <i-count-up
                      :start="0"
                      :endVal="getCurrentMonthData().orbit_count"
                      :duration="2"
                      :callback="callback"
                      ></i-count-up>
                    </b></td>
                  </table>
                </div>
              </div>
            </div>
            <div class="row plain-element">
              <div class="col s12 m5 plain-element">
                <div class="card insights-card insights-card-left">
                  <div class="card-header">
                    <p>March Summary</p>
                  </div>
                  <div v-if="getCurrentMonthData()" class="chart">
                    <table class="table-summary">
                      <tr>
                        <td>Modules Created:</td>
                        <td><b>{{getCurrentMonthData().new_modules}}</b></td>
                      </tr>
                      <tr>
                        <td>Budget Overhead:</td>
                        <td><b>{{getCurrentMonthData().budget_added}}</b></td>
                      </tr>
                      <tr>
                        <td>Budget Adjusted:</td>
                        <td><b>{{getCurrentMonthData().budget_adjusted}}</b></td>
                      </tr>
                    </table>
                    <br><br>
                    <div class="card-header">
                    <p>Weekly Productivity</p>
                    <br>
                  </div>
                    <table class="table-months">
                    <thead>
                      <tr>
                        <th>Week</th>
                        <th class="center">New Projects</th>
                        <th class="center">Budget</th>
                        <th class="center">Budget Adj.</th>
                      </tr>
                    </thead>
                    <tbody>
                          <tr v-for="(week, index) in getWeeksData()" :key="index">
                            <td><span class="week">Week</span> {{week.week_number}}</td>
                            <td class="center">{{week.new_modules}} </td>
                            <td class="center">{{week.total_difference}}</td>
                            <td class="center">{{week.total_adjustment}}</td>
                          </tr>

                    </tbody>
                    </table>
                  </div>
                </div>
              </div>
              <div class="col s12 m7 plain-element">
                <div v-if="getWeeksChartData()" class="card insights-card insights-card-right">
                  <div class="card-header">
                    <p>Completion By Week</p>
                  </div>
                  <div class="chart">
                    <weeks-chart :chart-data="getWeeksChartData()" :styles="weeksChartStyles"></weeks-chart>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="col s12 m12 l4 plain-element">
            <div class="row week-menu"> </div>
            <div class="row plain-element">
              <div v-if="getBrandChartData()" class="card insights-card insights-card-narrow-right">
                <div class="card-header">
                  <p>Completion By Manufacturer</p></div>
                <div class="chart chart-long">
                  <brand-chart :chart-data="getBrandChartData()" :styles="brandChartStyles"></brand-chart>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div  class="plain-element">
          <div class="plain-element">
            <div class="col sidebar"></div>
          </div>

          <div class="col s9 m10 plain-element">
            <div class="dashboard-charts">
            </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>




<script>
import { mapGetters, mapActions } from "vuex";
import ICountUp from 'vue-countup-v2';
import SidebarComponent from "@/components/SidebarComponent.vue"
import TopMenuComponent from "@/components/TopMenuComponent.vue"
import WeekMenuComponent from "@/components/WeekMenuComponent.vue"


import BrandChart from "@/common/BrandChart.js";
import WeeksChart from "@/common/WeeksChart.js";



export default {
  name: 'Month',
  components: {
    SidebarComponent,
    TopMenuComponent,
    WeekMenuComponent,
    BrandChart,
    WeeksChart,
    ICountUp,
  },
  props: {
    year: {
      type: String,
      required: true,
    },
    month: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
    }
  },
  methods: {
    ...mapGetters(["getCurrentYear","getBrandChartData", "getBrandChartHeight", "getCurrentMonth",
                  "getCurrentMonthData", "getWeeksChartData", "getWeeksChartWidth",
                  "getWeeksData", "getWeeksList"]),
    ...mapActions(["fetchMonthData"]),

    callback: function (ins) {
        ins.update(ins.endVal + 100)
    },


  },
  computed: {
   brandChartStyles () {
      return {
        height: `${this.getBrandChartHeight()}%`,
        position: "relative"
      }
   },
   weeksChartStyles () {
      return {
        height: `100%`,
        position: "relative",
        width: `${this.getWeeksChartWidth()}%`,
      }
   },
  },
  created() {
    this.fetchMonthData({"year": this.year, "month": this.month})

  },
  beforeRouteUpdate (to, from, next) {
    this.chosenYear = to.params.year;
    this.chosenMonth = to.params.month;
    this.fetchMonthData({"year": this.chosenYear, "month": this.chosenMonth});
    next();
  }
}
</script>