<template>
<div class="col sidebar">

  <div v-if="getYearList()" class="row section">
    <div class="row">
      <span class="section-header">Annual Reports</span>
    </div>
    <div v-for="(element, index) in getYearList()" :key="index" class="row">
      <router-link :to="{ name: 'year-data', params: {year: element.toString()}}">
        <div class="card menu-card">
          <span v-if="element == getCurrentYear()" class="section-element"><b><i class="fas fa-caret-right"></i>{{ element }}</b></span>
          <span v-else class="section-element"><i class="fas fa-caret-right"></i>{{ element }}</span>
        </div>
      </router-link>
    </div>
  </div>
  <div v-if="getMonthList()" class="row section">
    <div class="row">
      <span class="section-header">Monthly Reports</span>
    </div>
    <div v-for="(element, index) in getMonthList()" :key="index" class="row">
      <div  class="row plain-element">
        <router-link :to="{ name: 'month-data', params: {year: getCurrentYear(), month: element.toString() }}">
          <div v-if="element == currentMonth"  class="card menu-card active">
            <span class="section-element"><i class="fas fa-caret-right"></i>{{ monthMapperDict[element] }}</span>
          </div>
          <div v-else class="card menu-card">
            <span  class="section-element"><i class="fas fa-caret-right"></i>{{ monthMapperDict[element] }}</span>
           </div>
        </router-link>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { monthMapperDict } from "@/common/MonthMapper.js"
import { reverseMonthMapperDict } from "@/common/MonthMapper.js"
import { mapGetters, mapActions } from "vuex";


export default {
  name: "SidebarComponent",
  props: {
    currentYear: {
      type: String,
      required:true,
    },
    currentMonth: {
      type: String,
      required:false,
    },
    currentMonthNumber: {
      type: String,
      required:false,
    },
  },
  data() {
    return {
      monthMapperDict : monthMapperDict,
      reverseMonthMapperDict : reverseMonthMapperDict,
    }
  },
  methods: {
    ...mapGetters(["getYearList", "getMonthList", "getCurrentYear" ]),
    ...mapActions(["fetchMonths", "fetchAnnualData"]),
    getMonthNumber(month) {
      return reverseMonthMapperDict[month]
    },

  },
  computed: {
  },
  created() {
  }
}

</script>