import api from "../../api/api.js";


const state = {
  yearList: null,
  monthList: null,
  monthData: null,
  yearData: null,
  currentYear: null,
  brandChartData: {},
  brandChartHeight: null,
  monthChartData: {},
  monthChartWidth: null,
  currentMonth: null,
  currentMonthData: null,
  weeksData: null,
  weeksChartData: {},
  weeksChartWidth: null,
  weeksList: null,
  currentWeekData: null,
  moduleList: null,
  currentWeek: null,
  module: null,

}

const getters = {
  getYearList: state => state.yearList,
  getMonthList: state => state.monthList,
  getMonthData: state => state.monthData,
  getYearData: state => state.yearData,
  getCurrentYear: state => state.currentYear,
  getBrandChartData: state => state.brandChartData,
  getBrandChartHeight: state => state.brandChartHeight,
  getMonthChartData: state => state.monthChartData,
  getMonthChartWidth: state => state.monthChartWidth,
  getCurrentMonth: state => state.currentMonth,
  getCurrentMonthData: state => state.currentMonthData,
  getWeeksData: state => state.weeksData,
  getWeeksChartData: state => state.weeksChartData,
  getWeeksChartWidth: state => state.weeksChartWidth,
  getWeeksList: state => state.weeksList,
  getCurrentWeekData: state => state.currentWeekData,
  getModuleList: state => state.moduleList,
  getCurrentWeek: state => state.currentWeek,
  getModule: state => state.module,
}


const actions = {
  async fetchMonths({commit}, year) {
    const response = await api.monthsData(year);
    commit("setYearList", response.years)
    commit("setMonthList", response.months);
  },
  async fetchAnnualData({commit, dispatch}, year) {
    const response = await api.annualData(year);
    dispatch("fetchMonths", year);
    commit("setCurrentYear", year);
    commit("setMonthData", response.months_data);
    commit("setYearData", response);
    dispatch ("fillBrandChart", response.brand_count_dict);
    const brandHeight = Object.keys(response.brand_count_dict).length * (5 + ((20 - Object.keys(response.brand_count_dict).length) / 2.5));
    commit("setBrandChartHeight", brandHeight)
    dispatch ("fillMonthChart", response.month_list);
    const monthWidth = Object.keys(response.month_list).length * 8.3;
    commit("setMonthChartWidth", monthWidth)
  },
  async fetchMonthData({commit, dispatch}, payload) {
    const response = await api.monthData(payload.year, payload.month);
    dispatch("fetchMonths", payload.year);
    commit("setCurrentMonth", payload.month);
    commit("setCurrentYear", payload.year);
    commit("setCurrentMonthData", response);
    commit("setWeeksData", response.weeks_data);
    dispatch("fillBrandChart", response.brand_count_dict);
    const brandHeight = Object.keys(response.brand_count_dict).length * (5 + ((20 - Object.keys(response.brand_count_dict).length) / 2.5));
    commit("setBrandChartHeight", brandHeight);
    dispatch("fillWeeksChart", response.week_count_dict);
    const weeksWidth = Object.keys(response.week_count_dict).length * (20 + ((5 - Object.keys(response.week_count_dict).length) * 4));
    commit("setWeeksChartWidth", weeksWidth);
    dispatch("fetchWeeksData", {"year":payload.year, "month":payload.month})
  },
  async fetchWeeksData({commit, dispatch}, payload) {
     const response = await api.weeksData(payload.year, payload.month);
     dispatch("fetchMonths", payload.year);
     commit("setCurrentWeek", payload.week);
     commit("setCurrentMonth", payload.month);
     commit("setCurrentYear", payload.year);
     commit("setWeeksList", response.weeks);

  },
  async fetchModulesData({commit}, payload) {
    const response = await api.modulesData(payload.year, payload.month, payload.week);
    commit("setCurrentWeekData", response);
    commit("setModuleList", response.modules);
  },
  async fetchModuleData({commit}, string) {
    const response = await api.moduleData(string);
    commit("setModule", response.modules);
  },

  fillBrandChart({ commit }, dataset) {
    const dataLabelsBrandChart = Object.keys(dataset);
    const dataValuesBrandChart = Object.values(dataset);
    const chartData = {
      labels: dataLabelsBrandChart,
      datasets: [
        {
            backgroundColor: ["#0073b1","#0073b1","#0073b1", "#0073b1","#0073b1", "#0073b1","#0073b1","#0073b1",
                             "#0073b1", "#0073b1","#0073b1", "#0073b1", "#0073b1", "#0073b1", "#0073b1",
                             "#0073b1", "#0073b1","#0073b1", "#0073b1", "#0073b1"],
          data: dataValuesBrandChart
        }
      ]
    };
    commit("setBrandChartData", chartData)
  },
  fillMonthChart({ commit }, dataset) {
    const dataLabelsMonthChart = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const dataValuesMonthChart = Object.values(dataset);
    const chartData = {
      labels: dataLabelsMonthChart,
      datasets: [
        {
          backgroundColor: ["#0073b1","#0073b1","#0073b1", "#0073b1","#0073b1", "#0073b1","#0073b1","#0073b1",
                           "#0073b1", "#0073b1","#0073b1", "#0073b1", "#0073b1", "#0073b1", "#0073b1"],
          data: dataValuesMonthChart
        }
      ]
    };
    commit("setMonthChartData", chartData)
  },
  fillWeeksChart({ commit }, dataset) {
    const dataLabelsWeeksChart = Object.keys(dataset);
    const dataValuesWeeksChart = Object.values(dataset);
    const chartData = {
      labels: dataLabelsWeeksChart,
      datasets: [
        {
          backgroundColor: ["#0073b1","#0073b1","#0073b1", "#0073b1","#0073b1", "#0073b1","#0073b1","#0073b1",
                           "#0073b1", "#0073b1","#0073b1", "#0073b1", "#0073b1", "#0073b1", "#0073b1"],
          data: dataValuesWeeksChart
        }
      ]
    };
    commit("setWeeksChartData", chartData)
  },

}


const mutations = {
 setYearList: (state, yearList) => {
    state.yearList = yearList
 },
 setMonthList: (state, monthList) => {
    state.monthList = monthList
 },
 setMonthData: (state, monthData) => {
    state.monthData = monthData
 },
 setYearData: (state, yearData) => {
    state.yearData = yearData
 },
 setCurrentYear: (state, currentYear) => {
    state.currentYear = currentYear
 },
 setBrandChartData: (state, brandChartData) => {
    state.brandChartData = brandChartData
 },
 setBrandChartHeight: (state, brandChartHeight) => {
    state.brandChartHeight = brandChartHeight
 },
 setMonthChartData: (state, monthChartData) => {
    state.monthChartData = monthChartData
 },
 setMonthChartWidth: (state, monthChartWidth) => {
    state.monthChartWidth = monthChartWidth
 },
 setCurrentMonth: (state, currentMonth) => {
    state.currentMonth = currentMonth
 },
 setCurrentMonthData: (state, currentMonthData) => {
    state.currentMonthData = currentMonthData
 },
 setWeeksData: (state, weeksData) => {
    state.weeksData = weeksData
 },
 setWeeksChartData: (state, weeksChartData) => {
    state.weeksChartData = weeksChartData
 },
 setWeeksChartWidth: (state, weeksChartWidth) => {
    state.weeksChartWidth = weeksChartWidth
 },
 setWeeksList: (state, weeksList) => {
    state.weeksList = weeksList
 },
 setCurrentWeekData: (state, currentWeekData) => {
    state.currentWeekData = currentWeekData
 },
 setModuleList: (state, moduleList) => {
    state.moduleList = moduleList
 },
 setCurrentWeek: (state, currentWeek) => {
    state.currentWeek = currentWeek
 },
 setModule: (state, module) => {
    state.module = module
 },

}

export default {
  state,
  getters,
  actions,
  mutations
}











