<template>
<div class="row top-menu">
  <div class="row">
    <div v-for="(element, index) in getYearList()" :key="index"  class="col s3 m2 col-link">
      <router-link v-if="element == getCurrentYear()" class="btn btn-pill active" :to="{ name: 'year-data', params: {year: element.toString()}}">
              {{ element }}
      </router-link>
      <router-link v-else class="btn btn-pill" :to="{ name: 'year-data', params: {year: element.toString()}}">
              {{ element }}
      </router-link>
    </div>
  </div>
  <div v-if="getMonthList()" class="row">
    <div  v-for="(element, index) in getMonthList()" :key="index"  class="col s3 m2 col-link">
      <router-link v-if="element == currentMonth"  class="btn btn-pill active"  :to="{ name: 'month-data', params: {year: getCurrentYear(), month: element.toString() }}">
              {{ monthMapperDict[element] }}
      </router-link>
      <router-link v-else class="btn btn-pill"  :to="{ name: 'month-data', params: {year: getCurrentYear(), month: element.toString() }}">
              {{ monthMapperDict[element] }}
      </router-link>
    </div>
  </div>
</div>
</template>

<script>
import { monthMapperDict } from "@/common/MonthMapper.js"
import { reverseMonthMapperDict } from "@/common/MonthMapper.js"
import { mapGetters, mapActions } from "vuex";


export default {
  name: "TopMenuComponent",
    props: {
    currentYear: {
      type: String,
      required:true,
    },
    currentMonth: {
      type: String,
      required:false,
    },
    currentMonthNumber: {
      type: String,
      required:false,
    },
  },
  data() {
    return {
      monthMapperDict : monthMapperDict,
      reverseMonthMapperDict : reverseMonthMapperDict,
    }
  },
  methods: {
    ...mapGetters(["getYearList", "getMonthList", "getCurrentYear" ]),
    ...mapActions(["fetchMonths", "fetchAnnualData"]),
    getMonthNumber(month) {
      return reverseMonthMapperDict[month]
    },

  },
  computed: {
  },
  created() {
  }
}

</script>